$logo-width: 100px;
$logo-width-resp: 80px;

.nav {
  padding-top: 20px !important;
  padding-bottom: 20px !important;
  position: fixed;
  top: 0;
  width: 100vw;
  z-index: 1000;
  transition: background-color 0.3s ease;

  .navInner {
    padding-left: 0;
    transform: translateX(-$logo-width - 25px);
    transition: transform 0.3s ease;
  }

  .innerShift {
    transform: translateX(0);
  }

  a {
    color: #282828;
    font-size: 1.0625rem;
    font-weight: normal;
    padding: 0.5rem 1rem;
  }

  .logo {
    transition: transform 0.3s ease, opacity 0.3s ease;
    transform: translateY(-100%) translateX($logo-width + 25px);
    opacity: 0;
    width: $logo-width;
    margin-right: 3rem;
  }

  .logoVisible {
    transform: translateY(0) translateX(25px);
    opacity: 1;

    @media screen and (max-width: 991px) {
      padding: 0;
    }

    @media screen and (max-width: 575px) {
      transform: translateY(0) translateX(0);
    }
  }

  [class="nav-item"] {
    display: flex;
    align-items: center;
  }
}

.full {
  background-color: #FFFFFFF0;
}

.navResp {
  position: fixed;
  top: 0;
  width: 100vw;
  z-index: 10;
  background-color: white;

  // box-shadow: 5px 5px 5px 0 rgba(70, 115, 122, 0.1);

  .logo {
    width: $logo-width-resp;
  }
}

.navInnerResp {
  background-color: #FFFFFFBB;
  [class="nav-item"] {
    width: 100%;

    display: flex;
    justify-content: center;
    margin: 10px 0;
  }
}

.bars {
  height: 100%;
  background-color: #58BFB8 !important;
  padding: 5px 12px !important;
  margin: 10px;
  border-radius: 5px !important;

  i {
    color: white;
  }
}